.burger{
    width: 50px;
    height: 50px;
    background: transparent url("../../../img/menu.png") no-repeat;
    background-size: 200%;
    position: absolute;
    display: none;
}
.navbarMenu{
    position: relative;
    align-self: center;

}
.menu{

    list-style-type: none;
}
.menuItem{
    display: inline-block;
    margin-right: 20px;
    text-transform: uppercase;
    padding: 8px 15px;
    border-radius: 3px;
    transition: all 0.2s ease-in;
    font-size: 13px;
    font-weight: 700;
}

.menuItem a{
    text-decoration: none;
    color: #444;
}
.menuItem a.active{
    color: #F4716A;
    font-weight: 900;
}
.menuItem:hover{
    color: #ffffff;
    background-color:#FF7A73;
}
.menuItem:hover >a{
    color: #ffffff;
}

.dropdown{
    position: relative;
}
.dropdown:hover +.menuDropdown{
    display: block;
}

.menuDropdown{
    position: absolute;
    display: none;
}